.select-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 10px 0;
}

.select-label {
  font-weight: bold;
  margin-bottom: 5px;
}

.select-main {
  border: 1px black solid;
  padding: 0px 10px;
  height: 30px;
  border-radius: 2px;
  font-family: "Open Sans", sans-serif;
}
